	-webkit-box-shadow: 1px 1px black;
	        box-shadow: 1px 1px black;
	background-color: dodgerblue;
	color: aliceblue;
	-webkit-touch-callout: none;
	/* iOS Safari */
	-webkit-user-select: none;
	/* Safari */
	/* Konqueror HTML */
	-moz-user-select: none;
	/* Firefox */
	-ms-user-select: none;
	/* Internet Explorer/Edge */
	user-select: none;
	/* Non-prefixed version, currently
  	supported by Chrome and Opera */
}

.typingArea {
	position: absolute;
	width: 90%;
	margin-left: 5%;
	height: 150px;
	margin-bottom: 2%;
	margin-top: 20px;
	top: 40px;
	pointer-events: none;



}

.textbox {
	height: 80%;
	width: 100%;
	font-size: 4rem;
	font-family: 'Roboto Mono', monospace;
	resize: none;
	line-height: 80%;
	background-color: aliceblue;
	white-space: nowrap;
	overflow: hidden;
	position: absolute;
	border: inset 2px black;
	-webkit-box-shadow: 3px 3px;
	        box-shadow: 3px 3px; /* ff3.6+ */
background: -webkit-gradient(linear, left bottom, right top, color-stop(0%, #008080), color-stop(25%, #FFFFFF), color-stop(50%, #05C1FF), color-stop(75%, #FFFFFF), color-stop(100%, #005757)); /* safari4+,chrome */
background: -webkit-linear-gradient(45deg, #008080 0%, #FFFFFF 25%, #05C1FF 50%, #FFFFFF 75%, #005757 100%); /* safari5.1+,chrome10+ */
background: -o-linear-gradient(45deg, #008080 0%, #FFFFFF 25%, #05C1FF 50%, #FFFFFF 75%, #005757 100%); /* opera 11.10+ */ /* ie10+ */
background: linear-gradient(45deg, #008080 0%, #FFFFFF 25%, #05C1FF 50%, #FFFFFF 75%, #005757 100%); /* w3c */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#005757', endColorstr='#008080',GradientType=1 ); /* ie6-9 */
	

}
.innerTextBox {
    height: 60%;
	width: 100%;
	font-size: 4rem;
	font-family: 'Roboto Mono', monospace;
	resize: none;
	line-height: 80%;
	color: #000;
	background-color: aliceblue;
	/*white-space: nowrap;*/
	overflow: hidden;
	position: absolute;
	left: 40%;
	top: 30%;
	background-color: transparent;
	border: none;
	white-space: pre;
	display: inline-block;
	pointer-events: none;

	
}

.innerTextBox > span {
	background: yellow;
}
.innerTextBox > strong {
	background: red;
}


.innerTextBoxUsed {
    height: 60%;
	width: 100%;
	font-size: 4rem;
	font-family: 'Roboto Mono', monospace;
	resize: none;
	line-height: 80%;
	color: lightgray;
	background-color: aliceblue;
	white-space: nowrap;
	overflow: hidden;
	position: absolute;
	left: -62%;
	top: 30%;
	background-color: transparent;
	border: none;
	direction:rtl;
	unicode-bidi: bidi-override;
	pointer-events: none;
	
	
	
}
